export default {
	data() {
		return {
			count: '',
			timer: null,
			show: true,
			disabled:false,
			mobile:'',
			code:'',
			upwd:'',
			repassword:''
		};
	},
	methods:{
		edit(){
			if(this.mobile == ''){
				uni.showToast({
					title: '请输入手机号码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(!(/^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/.test(this.mobile))){
				uni.showToast({
					title: '手机号码格式不正确',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.code == ''){
				uni.showToast({
					title: '请输入验证码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.upwd == ''){
				uni.showToast({
					title: '请输入密码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.upwd != this.repassword){
				uni.showToast({
					title: '两次输入的密码不一致',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			uni.hideKeyboard()
			this.disabled = true
			this.$store.dispatch('forgetPassword',{
				mobile: this.mobile,
				code: this.code,
				upwd: this.upwd
			}).then(res=>{
				this.disabled = false
				uni.showToast({
					title: res.msg,
					success:()=>{
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						}, 1500);
					}
				})
			}).catch(err=>{
				this.disabled = false
			})
		},
		get_code(){
			this.$store.dispatch('signupCode',{
				mobile:this.mobile,
				type:'2'
			}).then(res=>{
				// 等待时长60s获取一次验证码
				const TIME_COUNT = 60;
				if (!this.timer) {
					this.count = TIME_COUNT;
					this.show = false;
					this.timer = setInterval(() => {
						if (this.count > 0 && this.count <= TIME_COUNT) {
							this.count--;
						} else {
							this.show = true;
							clearInterval(this.timer);
							this.timer = null;
						}
					}, 1000)
				}
			}).catch(err=>{})
		},
	},
	onLoad() {
		
	},
}
